import axios from 'axios';

export const registerUserWithEmail = (postData) => {
  return axios.post('https://ibougesever.herokuapp.com/auth/signup', postData);
};

export const registerResendEmail = (postData) => {
  return axios.post(
    'https://ibougesever.herokuapp.com/auth/resend-email',
    postData
  );
};
