import axios from 'axios';

export const getLocation = () => {
  return axios.get('https://ipinfo.io');
};

export const updateUser = (email, data) => {
  return axios.put(`https://ibougesever.herokuapp.com/users/${email}`, data);
};

export const changeProfilePic = (email, data) => {
  var fd = new FormData();
  fd.append('userId', data.userId);
  fd.append('userEmail', email);
  fd.append('albumName', data.albumName);
  if (data.file) {
    fd.append('file', data.file);
  }
  if (data.originalFile) {
    fd.append('originalFile', data.originalFile);
  }
  return axios.post(
    `https://ibougesever.herokuapp.com/users/profilepic/${email}`,
    fd
  );
};

export const loadMeta = (id) => {
  return axios.get(`https://ibougesever.herokuapp.com/users/meta?id=${id}`);
};
export const getUserMeta = (id) => {
  return axios.get(
    `https://ibougesever.herokuapp.com/users/get-user-meta?id=${id}`
  );
};
export const getAllUsers = (id) => {
  return axios.get(
    `https://ibougesever.herokuapp.com/users/get-all-users?id=${id}`
  );
};
// data api calls
export const getMyFriends = (id) => {
  return axios.get(`https://ibougesever.herokuapp.com/users/friends?id=${id}`);
};

export const getProfileFriends = (uid, pid) => {
  return axios.get(
    `https://ibougesever.herokuapp.com/users/friends?id=${uid}&profile=${pid}`
  );
};
export const loadBlockList = (email) => {
  return axios.get(
    `https://ibougesever.herokuapp.com/users/blocklist?email=${email}`
  );
};
export const loadInbox = (uid) => {
  return axios.get(`https://ibougesever.herokuapp.com/users/inbox?id=${uid}`);
};
export const loadNotifications = (uid) => {
  return axios.get(
    `https://ibougesever.herokuapp.com/users/notifications?id=${uid}`
  );
};

export const sendFriendRequest = (uId, friendId) => {
  return axios.post('https://ibougesever.herokuapp.com/users/friend-request', {
    id: uId,
    friend: friendId,
  });
};
export const getUserProfile = (_id, userId) => {
  return axios.get(
    `https://ibougesever.herokuapp.com/users/profile?id=${_id}&user=${userId}`
  );
};
export const acceptFriendRequest = (_id, userId) => {
  return axios.post(
    'https://ibougesever.herokuapp.com/users/accept-friend-request',
    {id: _id, user: userId}
  );
};

export const unfriend = (_id, userId) => {
  return axios.post('https://ibougesever.herokuapp.com/users/unfriend', {
    id: _id,
    user: userId,
  });
};

export const getMyDashboardMetaData = (_id) => {
  return axios.get(
    `https://ibougesever.herokuapp.com/my-dashboard/meta?id=${_id}`
  );
};
